.container {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 padding: 25px;
}

.input{
  padding: 16px;
  width: 80%;
  margin: auto;
  border: 1px solid lightgrey;
  border-radius: 7px;
  font-size: 16px
}

.input-focus {
  outline: none;
}

.weather-data {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.city {
  font-size: 30px;
  font-weight: 200;
  
}

.temp {
  font-size: 90px;
  padding: 10px;
  border: 1px solid lightgrey;
  border-radius: 12px;
}
  
.weather {
  font-size: 30px;
  font-weight: 200;
}

body {
  background-color: #D291BC;
  background-image: url('https://www.adrisworld.com/.a/6a0167691f7e7a970b01a511b8b07d970c-pi');
  background-size: cover;
  background-repeat: no-repeat;
}
